function slider() {
  let partnersSwiper = new Swiper('.partners-slide', {
    // Optional parameters
    effect: 'slide',
    slidesPerView: 'auto',
    spaceBetween: 46,
    slidesPerGroup: 1,
    //Стартовый слайд
    initialSlide: 0,

    navigation: {
      nextEl: '.partners-slide__btn_next',
      prevEl: '.partners-slide__btn_prev',
    },

    pagination: {
      el: '.partners-slide__pagination',
      // type: 'bullets',
      clickable: true,
      dynamicBullets: true,
      dynamicMainBullets: 3,
    },

    speed: 600,


    breakpoints: {
      320: {
        spaceBetween: 16,
        slidesPerView: 'auto',
        slidesPerGroup: 1,
      },
      581: {
        spaceBetween: 12,
        slidesPerView: 2,
        slidesPerGroup: 2,
      },
      769: {
        spaceBetween: 15,
        slidesPerView: 2,
        slidesPerGroup: 2,
      },
      993: {
        spaceBetween: 15,
        slidesPerView: 3,
        slidesPerGroup: 3,
      },
      1141: {
        spaceBetween: 15,
        slidesPerView: 2,
        slidesPerGroup: 2,
      },
      1541: {
        spaceBetween: 15,
        slidesPerView: 3,
        slidesPerGroup: 3,
      },
      1800: {
        spaceBetween: 30,
        slidesPerView: 3,
        slidesPerGroup: 3,

      },
      1920: {
        spaceBetween: 46,
        slidesPerView: 3,
        slidesPerGroup: 3,
      },

    },


  });




}


setTimeout(slider, 0);