function toggleMenu() {
  const menuBtn = document.querySelector('.menu-btn')
  const menu = document.querySelector('.header__menu')
  console.log(menuBtn);

  menuBtn.addEventListener('click', () => {

    if (!menuBtn.classList.contains('menu-btn_open-menu')) {
      openMenu()
    } else {
      closeMenu()
    }
  })

  menu.addEventListener('click', (event) => {
    const eventClass = event.target.classList
    if (eventClass.contains('nav-menu__link') || eventClass.contains('menu-list__link')) {
      closeMenu()
    }


  })

  function openMenu() {
    menu.classList.add('header__menu_ative')
    menuBtn.classList.add('menu-btn_open-menu')
  }

  function closeMenu() {
    menu.classList.remove('header__menu_ative')
    menuBtn.classList.remove('menu-btn_open-menu')
  }
}

